.wing_banner #myList {
  position: fixed;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
  z-index: 9;
}
.wing_banner #myList li {
  width: 40px;
  height: 40px;
  text-align: center;
  background-color: #eee;
  background: rgb(255, 255, 255);
  border-top: 1px solid rgb(223, 223, 223);
  border-left: 1px solid rgb(223, 223, 223);
  position: relative;
  cursor: pointer;
}
.wing_banner #myList li:last-child {
  border-bottom: 1px solid rgb(223, 223, 223);
}
.wing_banner #myList li i {
  font-size: 18px;
  line-height: 39px;
  color: #bbbaba;
  transition: all 0.3s;
}
.wing_banner #myList li i:hover {
  color: #333;
}
.wing_banner #myList li i.arrow {
  color: #333;
}
.wing_banner #myList li .txt_hide {
  background: #454545;
  position: absolute;
  top: 6px;
  left: -9px;
  padding: 0 10px;
  visibility: hidden;
  width: 66px;
  height: 28px;
  border-radius: 4px;
  border: 1px solid #474747;
  color: #fff;
  text-align: center;
  font-size: 12px;
  line-height: 28px;
  white-space: nowrap;
  transform: translate(-100%, 0);
  transition: all 0s 0s ease;
}
.wing_banner #myList li:hover .txt_hide {
  visibility: visible;
}

